.input {
  border-radius: 8px;
  border: 1px solid #bedbb0;
  opacity: 0.4;
  background: #1f1f1f;
  box-shadow: 0px 4px 16px 0px rgba(22, 22, 22, 0.08);
  padding: 14px 214px 14px 18px;
  align-items: center;
  color: #ffffff;
  font-size: 14px;
}

.input::placeholder {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.28px;
}
